body{background:#f3d7dc;font-family:'Press Start 2P', cursive}.main-container{width:100%;display:flex;flex-direction:column;align-items:center}.game-header{padding:2vh;font-size:45px;-webkit-text-stroke:1px black;-webkit-animation:color-change 1s infinite;animation:color-change 1s infinite}@-webkit-keyframes color-change{0%{text-shadow:4px 4px #e75959;color:#e75959}50%{text-shadow:4px 4px gold;color:gold}100%{text-shadow:4px 4px #e75959;color:#e75959}}@keyframes color-change{0%{text-shadow:4px 4px #e75959;color:#e75959}50%{text-shadow:4px 4px gold;color:gold}100%{text-shadow:4px 4px #e75959;color:#e75959}}.game-container{display:flex;font-size:20px}.controls-panel{box-sizing:border-box;display:flex;align-items:center;background:yellow}
